import React, { useEffect } from 'react';
import ExpertQuote from './ExpertQuote';
import StatBox from './StatBox';
import ContentChunk from './ContentChunk';
import FAQSchema from './FAQSchema';
import Waitlist from './Waitlist';
import { DEFAULT_SPECIFICATIONS_TITLE, getSpecIconFallback } from '@/lib/uiDefaults';
import { cn } from '@/lib/utils';
import { debugService } from '@/lib/debugService';

type ProductDetail = { title: string; description: string };
type ProductSpec = { icon: string; name: string; value: string };

type ProductLike = {
  llm?: {
    expertQuote?: {
      quote?: string;
      expertName?: string;
      expertTitle?: string;
      institution?: string;
    };
    statistic?: {
      statistic?: string;
      description?: string;
      source?: string;
      date?: string;
    };
    faqs?: Array<{ question: string; answer: string }>
  };
  details?: ProductDetail[];
  benefits?: string[];
  specifications?: ProductSpec[];
  visibility?: { 
    waitlist?: boolean;
    expertQuote?: boolean;
    statistic?: boolean;
    features?: boolean;
    benefits?: boolean;
    specifications?: boolean;
    faqs?: boolean;
  };
  sections?: {
    features?: { title?: string; description?: string; columns?: number };
    useCases?: { title?: string; description?: string; columns?: number };
    specifications?: { title?: string; description?: string; columns?: number };
  };
};

interface ProductContentSectionsProps {
  product: ProductLike;
  faqs: Array<{ question: string; answer: string }>;
  detailsOverride?: ProductDetail[];
  benefitsOverride?: string[];
  sectionOverrides?: {
    featuresTitle?: string;
    featuresDescription?: string;
    useCasesTitle?: string;
    useCasesDescription?: string;
    specsTitle?: string;
    specsDescription?: string;
  };
}

const ProductContentSections: React.FC<ProductContentSectionsProps> = ({
  product,
  faqs,
  detailsOverride,
  benefitsOverride,
  sectionOverrides
}) => {
  const colorClasses = ['icon-purple', 'icon-blue', 'icon-green', 'icon-orange', 'icon-pink', 'icon-indigo'];

  const getGridClassesForCount = (count: number): string => {
    const cols = Math.max(1, Math.min(count, 4));
    switch (cols) {
      case 1:
        return 'grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 gap-8';
      case 2:
        return 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 gap-8';
      case 3:
        return 'grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 gap-8';
      case 4:
      default:
        return 'grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 gap-8';
    }
  };

  const expertQuote = {
    quote: product?.llm?.expertQuote?.quote || '',
    expertName: product?.llm?.expertQuote?.expertName || '',
    expertTitle: product?.llm?.expertQuote?.expertTitle || '',
    institution: product?.llm?.expertQuote?.institution || '',
  };

  const hasExpertQuote = Boolean(expertQuote.quote);

  const statistic = {
    statistic: product?.llm?.statistic?.statistic || '',
    description: product?.llm?.statistic?.description || '',
    source: product?.llm?.statistic?.source || '',
    date: product?.llm?.statistic?.date || ''
  };

  const hasStatistic = Boolean(statistic.statistic || statistic.description);

  const details: ProductDetail[] | undefined = (detailsOverride && detailsOverride.length > 0)
    ? detailsOverride
    : product.details;

  const benefits: string[] | undefined = (benefitsOverride && benefitsOverride.length > 0)
    ? benefitsOverride
    : product.benefits;

  const featuresTitle = sectionOverrides?.featuresTitle
    || product?.sections?.features?.title
    || ' Key Features';
  const featuresDescription = sectionOverrides?.featuresDescription
    || product?.sections?.features?.description
    || 'Comprehensive design system analytics platform with automated Figma integration and ROI tracking';

  const useCasesTitle = sectionOverrides?.useCasesTitle
    || product?.sections?.useCases?.title
    || ' Use Cases';
  const useCasesDescription = sectionOverrides?.useCasesDescription
    || product?.sections?.useCases?.description
    || 'Perfect for product teams seeking data-driven design system optimization and ROI measurement';

  const specsTitle = sectionOverrides?.specsTitle
    || product?.sections?.specifications?.title
    || DEFAULT_SPECIFICATIONS_TITLE;
  const specsDescription = sectionOverrides?.specsDescription
    || product?.sections?.specifications?.description
    || 'Built with powerful features for professional design workflows';

  const sanitizeColumns = (value: any, fallback: number): number => {
    const parsed = typeof value === 'string' ? parseInt(value, 10) : value;
    if (Number.isNaN(parsed)) return fallback;
    return Math.min(4, Math.max(1, parsed));
  };
  const useCasesColumns = sanitizeColumns(product?.sections?.useCases?.columns, 2);
  const featuresColumns = sanitizeColumns(product?.sections?.features?.columns, Math.min(details?.length || 1, 4));
  const specsColumns = sanitizeColumns(product?.sections?.specifications?.columns, Math.min(product?.specifications?.length || 1, 4));

  // Debug: report computed columns and item counts
  useEffect(() => {
    try {
      debugService.info('ProductContentSections: computed grid settings', {
        featuresColumns,
        detailsCount: details?.length || 0,
        useCasesColumns,
        benefitsCount: benefits?.length || 0,
        specsColumns,
        specsCount: product?.specifications?.length || 0,
      });
    } catch {}
  }, [featuresColumns, details, useCasesColumns, benefits, specsColumns, product?.specifications]);

  const getGridClassesForColumns = (cols: number): string => {
    switch (cols) {
      case 1:
        return 'grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 gap-8';
      case 2:
        return 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 gap-8';
      case 3:
        return 'grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 gap-8';
      case 4:
      default:
        return 'grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 gap-8';
    }
  };

  return (
    <>
      {hasExpertQuote && product?.visibility?.expertQuote !== false && (
        <section className="py-12 px-4 bg-gray-50">
          <div className="container mx-auto max-w-4xl">
            <ExpertQuote {...expertQuote} />
          </div>
        </section>
      )}

      <div className="h-8" />

      {hasStatistic && product?.visibility?.statistic !== false && (
        <section className="py-8 px-4">
          <div className="container mx-auto">
            <div className="flex justify-center">
              <StatBox {...statistic} />
            </div>
          </div>
        </section>
      )}

      <div className="h-8" />

      {details && details.length > 0 && product?.visibility?.features !== false && (
        <section className="py-20 px-4 section-background">
          <div className="container mx-auto">
            <ContentChunk>
              <div className="text-center mb-16">
                <h2 className="text-3xl md:text-4xl font-bold mb-4">{featuresTitle}</h2>
                <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
                  {featuresDescription}
                </p>
              </div>
            </ContentChunk>

            <div className={cn(getGridClassesForColumns(featuresColumns))}>
              {details.map((detail: ProductDetail, index: number) => (
                <ContentChunk key={index}>
                  <div className="card relative">
                    <div className={`icon ${colorClasses[index % colorClasses.length]} mb-6`}>
                      {['', '', '', '', ''][index] || ''}
                    </div>
                    <h3 className="text-xl font-semibold mb-3">{detail.title}</h3>
                    <p className="text-muted-foreground">{detail.description}</p>
                  </div>
                </ContentChunk>
              ))}
            </div>
          </div>
        </section>
      )}

      <div className="h-8" />

      {benefits && benefits.length > 0 && product?.visibility?.benefits !== false && (
        <section className="py-20 px-4">
          <div className="container mx-auto">
            <ContentChunk>
              <div className="text-center mb-16">
                <h2 className="text-3xl md:text-4xl font-bold mb-6">{useCasesTitle}</h2>
                <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
                  {useCasesDescription}
                </p>
              </div>
            </ContentChunk>

            <div className={cn(getGridClassesForColumns(useCasesColumns), 'max-w-4xl mx-auto')}>
              {benefits.map((benefit: string, index: number) => (
                <ContentChunk key={index}>
                  <div className="flex items-start">
                    <div className="w-8 h-8 rounded-full bg-primary/10 flex items-center justify-center mr-4 mt-0.5 flex-shrink-0">
                      <svg className="w-5 h-5 text-primary" fill="currentColor" viewBox="0 0 20 20">
                        <path
                          fillRule="evenodd"
                          d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </div>
                    <span className="text-lg">{benefit}</span>
                  </div>
                </ContentChunk>
              ))}
            </div>
          </div>
        </section>
      )}

      <div className="h-8" />

      {product.specifications && product.specifications.length > 0 && product?.visibility?.specifications !== false && (
        <section className="py-20 px-4 section-background">
          <div className="container mx-auto">
            <ContentChunk>
              <div className="text-center mb-16">
                <h2 className="text-3xl md:text-4xl font-bold mb-6">{specsTitle}</h2>
                <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
                  {specsDescription}
                </p>
              </div>
            </ContentChunk>

            <div className={cn(getGridClassesForColumns(specsColumns))}>
              {product.specifications.map((spec: ProductSpec, index: number) => (
                <ContentChunk key={index}>
                  <div className="card relative">
                    <div className={`icon ${colorClasses[index % colorClasses.length]} mb-6`}>{spec.icon || getSpecIconFallback(index)}</div>
                    <h3 className="text-xl font-semibold mb-3">{spec.name}</h3>
                    <p className="text-muted-foreground">{spec.value}</p>
                  </div>
                </ContentChunk>
              ))}
            </div>
          </div>
        </section>
      )}

      <div className="h-8" />

      {product?.visibility?.faqs !== false && (
        <section className="py-20 px-4 bg-gray-50">
          <div className="container mx-auto">
            <FAQSchema faqs={faqs} />
          </div>
        </section>
      )}

      <div className="h-8" />

      {product?.visibility?.waitlist !== false && (
        <Waitlist visibleOverride={product?.visibility?.waitlist} />
      )}
    </>
  );
};

export default ProductContentSections;


